.divBusiness {
  overflow: auto;
  height: calc(100vh - 348px);
}

.divBusiness::-webkit-scrollbar-thumb {
  background-color: #D2D2D2;
}

.divBusiness::-webkit-scrollbar {
  background-color: #D2D2D233;
  width: 14px;
  padding: 5px;
}

.assetListTitle {
  border-bottom: 1px solid #343A40;
  width: 100%;
}

.businessHead {
  border-bottom: 1px solid #ccc;
  margin-bottom: 10px;
  width: 100%;
}

.txtBox {
  font-size: 14px;
  padding: 5px;
  padding-top: 8px;
  padding-bottom: 8px;
}

.fastSubLabel {
  font-size: 14px;
  margin-left: 65px;
  color: #343a40;
}

.businessLabel {
  margin-top: -4px;
}

.businessLabel[disabled] label {
  color: #66696c;
  font-weight: normal;
}

.businessLabel label {
  font-weight: bold;
}

.businessLabel[disabled] > .inputLabel,
.businessLabel[disabled] > .primetimeDaypartLabel,
.businessLabel[disabled] > .fastSubLabel {
  color: #B1B1B1;
  font-weight: normal;
}

.select {
  min-width: 95px;
  width: 100%;
  margin-top: 4px;
}

.formInput {
  margin-top: 7px;
  padding-top: -220px;
  height: 33px;
  width: 95px;
  padding: 8px 13px;
  color: #343a40;
  font-size: 12px;
  line-height: 14.32px;
  border-radius: 4px;
  border: 1px solid #d3d2d2;
}

.formInput:focus {
  outline: none;
}

.formInput:disabled {
  cursor: not-allowed;
  background-color: #CED4DA;
  border: 1px solid #CED4DA;
  color: #B1B1B1;
}

.formInput:disabled::placeholder {
  color: #B1B1B1;
}

.inputLabel {
  margin-top: 4px;
  width: auto;
  font-size: 14px;
  margin-left: 60px;
  display: flex;
}

.inputLabel > span {
  margin-top: 10px !important;
}

.importBusinessRules {
  width: 100%;
  margin-top: 30px;
}

.importBusinessRulesHeader {
  width: 100%;
  font-weight: 600;
  font-size: 16px;
  color: #343A40;
  box-shadow: inset 0px -1px 0px rgba(52, 58, 64, 0.5);
  margin-bottom: 20px;
}

.importBusinesRulesHeaderActionIcons:nth-child(1) {
  margin-right: 10px;
}

.importBusinesRulesHeaderActionIcons:nth-child(2) {
  margin-right: 20px;
}

.importBusinesRulesHeaderActionIcons {
  cursor: pointer;
}

.importBusinessRulesHeaderActions,
.importBusiessRulesSearchWrapper {
  display: flex;
  width: 100%;
}

.importBusiessRulesSearch {
  border-radius: 4px 0px 0px 4px;
  border: 1px solid #CED4DA;
  border-right: none;
  width: 100%;
  height: 28px;
  font-weight: 400;
  font-size: 12px;
  text-indent: 5px;
  background-color: transparent;
}

.importBusiessRulesSearch:focus {
  outline: none;
}

.importBusiessRulesSearchIcon {
  border-radius: 0px 4px 4px 0px;
  border: 1px solid #CED4DA;
  border-left: none;
  padding-right: 8px;
  cursor: pointer;
}

.importBusiessRulesClearSearchIcon {
  border: 1px solid #CED4DA;
  border-left: none;
  border-right: none;
  padding-right: 8px;
  cursor: pointer;
}

.importBusiessRulesSearch::placeholder {
  color: #bd9797;
}

.businessRulesList {
  max-height: 350px;
  overflow-y: auto;
  width: 100%;
}

.businessRulesItem {
  display: flex;
  margin-top: 10px;
}

.businessRulesItem:nth-child(1) {
  margin-top: 20px;
}

.businessRuleDescription,
.businessRuleValue {
  width: 100%;
  border-radius: 4px;
  border: 1px solid #CED4DA;
  padding: 6px 13px;
  font-weight: 400;
  font-size: 12px;
}

.errorInput {
  border: 1px solid #FF7878;
}

.businessRuleDescription:focus,
.businessRuleValue:focus {
  outline: none;
}

.businessRuleDescription:disabled {
  color: #6D6D73;
  background-color: #CED4DA;
  cursor: not-allowed;
}

.businessRuleValue {
  color: #343A40;
  background-color: transparent;
  background-color: transparent;
}

.businessRuleValue::placeholder {
  color: #bd9797;
}

.trashCanSmIcon {
  margin-top: 4px;
  cursor: pointer;
}

.noCustomResultWrapper {
  text-align: center;
  height: 50px;
  padding-top: 17px;
  color: #6D6D73;
}

.customBusinessRulesLabel,
.assetListLabel {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow-x: hidden;
  font-size: 14px;
  width: 100%;
}

.customBusinessRulesLabel:nth-child(1) {
  width: 50%;
  margin-right: 10px;
}

.customBusinessRulesLabel:nth-child(2) {
  margin-right: 10px;
}

.assetListLabel:nth-child(1) {
  width: 147px;
}

.assetListLabel:nth-child(2) {
  width: 75px;
}

.assetListLabel:nth-child(4) {
  width: 240px;
}

.attributeToCheckSelect{
  padding-top: 3px;
}

.assetList {
  width: 100%;
  max-width: 1198px;
  border: 1px solid #DEE2E6;
  border-radius: 4px;
  padding: 10px;
}

.assetListTitles {
  margin-bottom: 10px;
}

.orderingButton {
  height: 26px;
  width: 26px;
  background-color: #008662;
  border: 1px solid #008662;
  border-radius: 4px;
  display: block;
  margin-bottom: 3px;
  margin-left: 3px;
}

.orderingButton:disabled {
  background-color: #CED4DA;
  border: 1px solid #CED4DA;
  color: #B1B1B1;
  cursor: not-allowed;
}

.orderingButton:disabled img {
  filter: brightness(0) saturate(100%) invert(87%) sepia(0%) saturate(7%) hue-rotate(146deg) brightness(85%) contrast(83%);
}

.inputLabel > span {
  margin-top: 5px;
}

.inputLabel > span:first-child {
  margin-right: 5px;
}

.inputLabel > span:last-child {
  margin-left: 3px;
