.tabList {
  /* border-bottom: 1px solid #ccc; */
  padding-left: 0;
  margin-bottom: 0;
}

.tabListItem {
  display: inline-block;
  list-style: none;
  margin-bottom: -1px;
  padding: 0.5rem 0.75rem;
}

.tabListActive {
  background-color: white;
  box-shadow: 0px 70px 150px rgb(0 0 0 / 15%);
  color: #008662;
  font-weight: bolder;
  border-radius: 5px 5px 0 0px;
}


.tabHeader {
  cursor: pointer;
  width: 145px;
  text-align: center;
}

.tabContent {
  background: #ffffff;
  box-shadow: 0px 15px 50px rgb(0 0 0 / 15%);
  padding: 25px;
  width: 100%;
  height: calc(100vh - 220px);
  overflow: auto;
}
