.profileView {
  display: flex;
  width: 100%;
}

.column {
  width: 33%;
}

.card {
  width: 100%;
  margin-top: 40px;
  position: relative;
}

.card:first-child {
  margin-top: 0px;
}

.categoryLabel {
  font-weight: 600;
  font-size: 16px;
  color: #343a40;
  box-shadow: inset 0px -1px 0px rgba(52, 58, 64, 0.5);
  margin-right: 25px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.probabilitiesList {
  display: flex;
  padding-right: 25px;
  margin-top: 15px;
  width: 100%;
  flex-wrap: wrap;
}

.probabilityItem {
  display: flex;
  width: 50%;
  margin-top: 10px;
  padding-left: 10px;
}

.probabilityItem:nth-child(1),
.probabilityItem:nth-child(2) {
  margin-top: 0px;
}

.probabilityLabel {
  width: 100%;
  overflow-x: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.percentText {
  margin-left: 5px;
}

.colon {
  margin-right: 5px;
}

.percentText,
.probabilityLabel,
.colon {
  text-align: right;
  font-weight: 400;
  font-size: 14px;
  margin-top: 7px;
  color: #343a40;
}

.probabilityInput {
  width: 100%;
  height: 34px;
  border-radius: 4px;
  font-size: 12px;
  padding: 8px 15px;
  border: 1px solid #d4d9df;
  text-align: center;
}

.probabilityInput::-webkit-inner-spin-button,
.probabilityInput::-webkit-outer-spin-button {
  -webkit-appearance: none;
  -moz-appearance: textfield;
  margin: 0;
}

.probabilityInput:focus {
  outline: none;
}

@media only screen and (max-width: 1200px) {
  .profileView {
    display: block;
  }

  .column {
    width: 100%;
  }

  .probabilitiesList {
    display: block;
  }

  .probabilityItem {
    margin-bottom: 5px;
  }
}
