.categoryHead {
  border-bottom: 1px solid #ccc;
  margin-bottom: 10px;
  width: 100%;
}

.divRatings {
  /* width: 100%; */
  margin: 10px;
  text-align: right;
  margin-right: 40%;
}

.divGenreRatings {
  width: 100%;
  margin: 10px;
  text-align: right;
  white-space: nowrap;
  display: flex;
  /* margin-right: 30%; */
}

.dataLabel {
  width: 100%;
  text-overflow: ellipsis;
  overflow-x: hidden;
  margin-top: 5px;
}

.inputNumber {
  width: 60px !important;
  margin-left: 5px;
  margin-right: 5px;
}

.textMore {
  margin-left: 5px;
  margin-right: 80px;
  color: #008662;
  cursor: help;
}

.textMoreMid {
  margin-left: 5px;
  margin-right: 3vw;
  color: #008662;
  cursor: help;
}

.profileBody {
  height: calc(100vh - 348px);
  overflow-y: auto;
  margin-right: -12px;
}

.profileBody::-webkit-scrollbar-thumb {
  background-color: #D2D2D2;
}

.profileBody::-webkit-scrollbar {
  background-color: #D2D2D233;
  width: 14px;
  padding: 5px;
}

.divProfile label {
  font-size: 14px;
}

.divProfile input[type='number']::-webkit-outer-spin-button,
input[type='number']::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.divProfile input[type='number'] {
  -moz-appearance: textfield;
}

.divProfile input[type='number'] {
  text-align: center;
}

.divCertificate > div {
  display: inline-block;

  /* padding: 5px; */
  /* margin: 10px;
  float: right; */
}

.divCertificate {
  height: 20px;
}

.textMoreCertificate {
  color: #008662;
  margin-top: 7px;
  cursor: help;
}

.formInput {
  width: 100%;
  padding: 8px 13px;
  color: #343a40;
  font-size: 12px;
  line-height: 14.32px;
  border-radius: 4px;
  border: 1px solid #d3d2d2;
}

.wrapper {
  height: 100%;
  width: 100%;
  position: relative;
}

.wrapperContent {
  position: absolute;
  height: 100px;
  width: 100%;
  top: 0;
  bottom: 0;
  margin: auto;
  text-align: center;
}

.wrapperContent h4 {
  color: #008662;
  margin-bottom: 20px;
}

.form {
  width: 100%;
  padding: 20px 0px;
}

.header {
  width: 100%;
}

.loadProfileText {
  line-height: 35px;
  font-size: 14px;
  margin-right: 10px;
}

.profileView {
  width: 100%;
  margin-top: 20px;
}

.modal {
  padding: 30px;
}

.searchChannelInput {
  border: 1px solid #b1b1b1;
  border-radius: 4px 0px 0px 4px;
  border-right: none;
  font-size: 12px;
  text-indent: 5px;
  width: 100%;
}

.searchChannelInput:focus {
  outline: none;
}

.searchChannelInput::placeholder {
  color: #b1b1b1;
}

.searchChannelIcon {
  content: url('../../../assets/icons/Search.png');
}

.searchChannelIcon:hover {
  content: url('../../../assets/icons/Search-Green.png');
}

.clearSearchChannelIcon {
  content: url('../../../assets/icons/Close.png');
  margin-top: 7px;
  filter: brightness(0) saturate(100%) invert(55%) sepia(7%) saturate(244%)
    hue-rotate(165deg) brightness(94%) contrast(89%);
  cursor: pointer;
}

.clearSearchChannelIcon:hover {
  content: url('../../../assets/icons/Close-Green.png');
}

.searchChannelIcon {
  cursor: pointer;
  height: 15px;
  margin-top: 7px;
}

.clearSearchChannelIconWrapper {
  border: 1px solid #b1b1b1;
  border-left: none;
  border-right: none;
  padding-right: 5px;
}

.searchChannelIconWrapper {
  border: 1px solid #b1b1b1;
  border-radius: 0px 4px 4px 0px;
  border-left: none;
  padding-right: 10px;
}

.channelList {
  margin-top: 20px;
  max-height: 300px;
  overflow-y: auto;
}

.channelList::-webkit-scrollbar-thumb {
  background-color: #d2d2d2;
  border-left: 1px solid #d2d2d2;
}

.channelList::-webkit-scrollbar {
  border: 1px solid #d2d2d2;
  background-color: #ffffff;
  width: 14px;
  box-shadow: offset 0 0 0 3px #ffffff;
}

.modalActionButtons {
  float: right;
  margin-top: 20px;
}

.channelListItem {
  display: flex;
  margin-bottom: 10px;
  cursor: pointer;
}

.channelListItem:hover {
  background-color: #eaeaea;
}

.selectDaysModalBody {
  padding: 25px 20px;
}

.selectDaysModalBody label {
  font-size: 20px;
}

.checkBox,
.activeCheckBox {
  height: 23px;
  width: 23px;
  border-radius: 2px;
  margin-right: 10px;
  user-select: none;
}

.checkBox {
  border: 1px solid #008662;
  background-color: #ffffff;
}

.activeCheckBox {
  background-color: #008662;
  padding-left: 4px;
}

.checkIcon {
  content: url('../../../assets/icons/Check-White-Large.svg');
  margin-left: 3px;
  margin-bottom: 5px;
}

.checkIcon2 {
  content: url('../../../assets/icons/Check-White-Large.svg');
  margin-left: 1px;
  margin-bottom: 1px;
}

.dayName {
  padding-left: 10px;
  color: #343A40;
  font-size: 14px;
}

.dayListItem {
  display: flex;
  margin-top: 15px;
  margin-bottom: 15px;
}

.channelListItemLabel {
  font-size: 14px;
  color: #343a40;
  line-height: 23px;
}

.daySection,
.startTimeSection,
.endTimeSection {
  display: flex;
}

.startTimeSection,
.daySelect,
.startTimeSelect,
.endTimeSwitch,
.endTimeSelect, 
.profileSelect {
  margin-left: 15px;
}

.endTimeSwitch {
  padding-top: 4px;
  padding-right: 0;
}

.headerButtons {
  display: flex;
}

.daySelect {
  width: 265px;
  padding-left: 0px;
}

.daySectionText {
  margin-top: 4px;
  font-size: 14px;
}

.copyIcon {
  cursor: pointer;
  margin-left: 10px;
}

.startTimeSelect,
.endTimeSelect {
  width: 190px;
}

.formContent {
  display: flex;
  width: 100%;
  height: calc(100vh - 424px);
  overflow-y: auto;
  margin-top: 20px;
}

.formContent::-webkit-scrollbar-thumb {
  background-color: #D2D2D2;
}

.formContent::-webkit-scrollbar {
  background-color: #D2D2D233;
  width: 14px;
  padding: 5px;
}

.formFooter {
  text-align: end;
  width: 100%;
  margin-top: 20px;
}

.headerContent {
  display: flex;
  width: 100%;
}

.column {
  width: 33%;
}

.column + .column {
  padding-left: 10px;
}

.loadProfileButton {
  display: flex;
  width: 375px;
  margin-right: 15px;
}

.applyToOtherChannelsButton {
  width: auto;
}

.divProfile {
  width: 100%;
  padding-top: 20px;
}

.checkbox {
  cursor: pointer;
  width: 22px;
  height: 22px;
  border-radius: 2px;
  border: 1px solid #008662;
}

.activeCheckbox {
  background-color: #008662;
}

.disabledCheckbox {
  border: 1px solid #B1B1B1;
  cursor: not-allowed;
}

.disabledCheckboxText {
  color: #B1B1B1 !important;
}

.checkIcon {
  height: 10px;
  width: auto;
}

.selectDaysModal {
  width: 320px;
}

.applySelectedDayConfirmationModal {
  max-width: 600px;
  text-align: center;
}

.appliedProfileValuesMessage {
  max-width: 550px;
  text-align: center;
}

.startTimeLabel {
  margin-top: 4px;
  font-size: 14px;
  padding: 0;
}

.applyProfileModalBody,
.selectedDayConfirmationModal {
  padding: 30px 50px;
}

.applyProfileModalBody p {
  font-size: 18px;
  font-weight: 100;
}

.proceedConfirmationText {
  font-size: 20px;
  font-weight: 300;
}

.selectedDayConfirmationModal p {
  font-size: 18px;
  font-weight: 100;
}

@media only screen and (max-width: 1200px) {
  .profileView {
    height: calc(100vh - 500px);
  }

  .headerButtons {
    display: block;
  }

  .startTimeSection {
    margin-left: 0px;
    margin-top: 20px;
  }

  .endTimeSwitch {
    margin-left: -5px;
  }

  .startTimeLabel,
  .endTimeSwitch {
    white-space: nowrap;
  }

  .endTimeSwitch,
  .endTimeSelect {
    margin-top: 20px;
  }

  .endTimeSwitch {
    width: 150px;
  }

  .daySelect,
  .startTimeSelect,
  .endTimeSelect,
  .profileSelect {
    width: 100%;
  }

  .formContent {
    display: block;
  }

  .column {
    width: 100%;
  }
}

@media only screen and (max-width: 800px) {
  .profileBody {
    height: calc(100vh - 368px);
  }
}

@media only screen and (max-width: 745px) {
  .headerContent {
    display: block;
  }

  .loadProfileButton {
    width: 100%;
    margin-bottom: 15px;
  }

  .formContent {
    height: calc(100vh - 420px);
  }
}

@media only screen and (max-width: 600px) {
  .profileBody {
    height: calc(100vh - 390px);
  }

  .endTimeSection,
  .startTimeSection,
  .daySection {
    display: block;
  }

  .endTimeSelect,
  .startTimeSelect,
  .daySelect, 
  .profileSelect {
    margin-left: 0px;
  }

  .endTimeSelect {
    margin-top: 7px;
  }
}

.saveModalButton {
  height: 36px;
  font-size: 12px;
  line-height: 12px;
  font-weight: 100;
  padding: 11px 24px;
  background-color: #008662;
  color: #ffffff;
  border: none;
  border-radius: 4px;
  width: 100%;
  margin-top: 20px;
}
.cancelButton {
  height: 36px;
  font-size: 12px;
  line-height: 12px;
  font-weight: 100;
  padding: 11px 24px;
  background-color: #f6f6f6;
  color: #5c636a;
  border: none;
  border-radius: 4px;
  width: 100%;
  margin-top: 20px;
}

.profileSelect{
  width: 231px;
  padding-left: 0px;
}