.input {
    width: 200px;
    height: 34px;
    border-radius: 4px;
    font-size: 12px;
    padding: 8px 15px;
    border: 1px solid #d4d9df;    
    text-align: center;
  }
  
  .input::-webkit-inner-spin-button,
  .input::-webkit-outer-spin-button {
    -webkit-appearance: none;
    -moz-appearance: textfield;
    margin: 0;
  }
  
  .input:focus {
    outline: none;
  }
  
  .tbody{
    overflow: auto;
    height: calc(100vh - 500px);
    display: block;
    width: 100%;
  }

  .tbody::-webkit-scrollbar-thumb {
    background-color: #D2D2D2;
  }
  
  .tbody::-webkit-scrollbar {
    background-color: #D2D2D233;
    width: 14px;
    padding: 5px;
  }

  .thead tr, .tbody tr{
    display: table;
    border-style: hidden;
  }
  .table {
    width: 100%;
}
.table>:not(:first-child) {
    border:none;
}
.tableRowWidth{
  width: 200px;
}
.tableHeaderRowWidth{
width: 215px;
}
.tableValueRowWidth{
  width: 240px;
}

.thead{
  text-align: center;
}

