.marginBottom {
  margin-bottom: 10px;
}

.channelHeader {
  border-bottom: 1px solid #ccc;
  margin-bottom: 10px;
}

.channelLabel {
  height: 100%;
  display: flex;
  align-items: center;
  width: 80px;
  margin-top: 4px;
}

.saveButton {
  width: 150px;
  height: 36px;
  font-size: 12px;
  line-height: 12px;
  font-weight: 100;
  padding: 11px 24px;
  color: #ffffff;
  background-color: #008662;
  border: none;
  border-radius: 4px;
}

.saveButton + .saveButton {
  margin-left: 20px;
}

.cancelButton {
  height: 36px;
  font-size: 12px;
  line-height: 12px;
  font-weight: 100;
  padding: 11px 24px;
  background-color: #f6f6f6;
  color: #5c636a;
  border: none;
  border-radius: 4px;
  width: 100%;
  margin-top: 20px;
  margin-left: 0;
  background: none;
}

.saveModalButton {
  height: 36px;
  font-size: 12px;
  line-height: 12px;
  font-weight: 100;
  padding: 11px 24px;
  background-color: #008662;
  color: #ffffff;
  border: none;
  border-radius: 4px;
  width: 100%;
  margin-top: 20px;
}

.daySelect {
  width: 400px;
}

.faCircleNotchSpin {
  font-size: 50px;
  color: #13ad83;
}

@media only screen and (max-width: 800px) {
  .daySelect {
    width: 100%;
  }

  .saveButton {
    display: block;
    width: 100%;
  }

  .saveButton + .saveButton {
    margin-top: 15px;
  }
}

.profileViewFooter {
  text-align: end;
  width: 100%;
  margin-top: 20px;
}

.promptModalBody {
  padding: 35px;
  text-align: center;
}

.promptModalBodyButtons {
  padding-top: 30px;
}

.activeTabContent {
  height: calc(100vh - 275px);
  position: relative;
  width: 100%;
}

.divider {
  background: rgb(200, 200, 200);
}