.timeRange {
    display: flex;
    width: 100%;
    border-radius: 4px;
    border: 1px solid #CED4DA;
    user-select: none;
    height: 32px;
}

.errorBorder {
    border: 1px solid #FF7878;
}

.errorBorder > div > .startTimeInput,
.errorBorder > div > .endTimeInput,
.errorBorder > div > .icon {
    border: none;
}

.disabledTimeRange,
.disabledTimeRange > .clockIcon,
.disabledTimeRange > div > .startTimeInput,
.disabledTimeRange > div > .endTimeInput {
    background-color: #CED4DA;
    cursor: not-allowed;
}

.disabledTimeRange > div > .startTimeInput,
.disabledTimeRange > div > .endTimeInput {
    pointer-events: none;
    color: #B1B1B1;
}

.disabledTimeRange > div > img {
    filter: brightness(0) saturate(100%) invert(92%) sepia(0%) saturate(256%) hue-rotate(207deg) brightness(79%) contrast(85%);
}

.startTimeInput, .endTimeInput {
    width: 100%;
    color: #6D6D73;
    font-weight: 400;
    font-size: 12px;
    height: 31px;
    padding-right: 10px;
    border: none;
    background-color: transparent;
}

.startTimeInput {
    padding-left: 20px;
}

.startTimeInput::placeholder,
.endTimeInput::placeholder {
    color: #b1b1b1;
}

.startTimeInput:focus, .endTimeInput:focus {
    outline: none;
}

.icon {
    width: 30px;
    min-width: 30px;
    padding-top: 2px;
}

.clockIcon {
    cursor: pointer;
    position: relative;
}

.startTimeDropdown {
    z-index: 1;
    position: absolute;
    border: 1px solid #CED4DA;
    background-color: #FFFFFF;
    padding: 7px;
    border-radius: 0px 0px 4px 4px;
    right: 0;
    margin-right: 20px;
    display: flex;
    width: 91px;
}

.hourInput, .minuteInput {
    width: 30px;
    text-align: center;
    border: 1px solid #CED4DA;
    border-radius: 4px;
    color: #6D6D73;
    font-weight: 400;
    font-size: 12px;
    height: 30px;
}

.hourInput:focus, .minuteInput:focus {
    outline: none;
}

.semiColon {
    margin-top: 27px;
    margin-right: 5px;
    margin-left: 5px;
}

.chevGreyIcon {
    margin-left: 10px;
}